.Dashboard .Navbar {
  position: initial; }
  .Dashboard .Navbar .Navbar-list .list-item-bus {
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    padding: 10px;
    cursor: pointer;
    text-align: justify; }
    .Dashboard .Navbar .Navbar-list .list-item-bus span {
      color: #fff; }
    .Dashboard .Navbar .Navbar-list .list-item-bus span:first-of-type {
      font-weight: bold;
      font-size: 14px; }
    .Dashboard .Navbar .Navbar-list .list-item-bus span:last-of-type {
      font-size: 12px; }
    .Dashboard .Navbar .Navbar-list .list-item-bus p:first-of-type {
      font-size: 20px; }
    .Dashboard .Navbar .Navbar-list .list-item-bus p {
      font-size: 12px;
      margin: 10px;
      color: #fff; }
  .Dashboard .Navbar .Navbar-list .list-item-bus:hover {
    background: rgba(255, 255, 255, 0.2); }

.Dashboard-BoxInfo {
  padding: 20px 5%; }

@media (min-width: 1024px) {
  .Dashboard .menuActive .Navbar-list {
    margin: 0;
    display: block; }
  .Dashboard .Navbar-list {
    position: absolute;
    height: auto;
    top: 83px;
    width: 350px;
    box-shadow: -5px 5px 20px rgba(80, 80, 80, 0.5);
    padding-top: 0;
    margin-right: 0;
    display: none;
    background: linear-gradient(to bottom, #0893c3, rgba(141, 222, 255, 0.8)); }
  .Dashboard-BoxInfo {
    display: flex; } }



