.scroll {
  overflow: hidden;
  height: 100%;
  width: 100%;
  position: fixed; }

svg {
  cursor: pointer;
  position: relative; }

.menuActive .Navbar-list {
  margin: 0;
  box-shadow: -5px 0px 25px rgba(80, 80, 80, 0.5); }

.search {
  width: 85%;
  margin: 20px;
  padding: 5px 10px;
  outline: none; }

.Navbar {
  position: relative;
  top: 10px;
  right: -10px; }
  .Navbar-list {
    list-style-type: none;
    width: 270px;
    margin: 0;
    margin-right: -270px;
    position: fixed;
    top: 0px;
    right: 0;
    height: 100%;
    transition: .5s;
    text-align: right;
    background: linear-gradient(to bottom, #0893c3, #8ddeff);
    padding: 0;
    padding-top: 80px;
    z-index: 1; }
    .Navbar-list .closeMenu {
      position: absolute;
      top: 0;
      padding: 20px;
      color: #fff;
      font-size: 20px;
      cursor: pointer; }
    .Navbar-list a {
      text-decoration: none;
      color: #fff;
      transition: .3s;
      display: block;
      padding: 20px;
      font-size: 16px; }
    .Navbar-list a:hover {
      color: #8DDEFF; }

@media (min-width: 1024px) {
  .Home .Navbar {
    top: 0;
    right: -40px; }
    .Home .Navbar svg {
      display: none; }
    .Home .Navbar-list {
      list-style-type: none;
      width: auto;
      height: auto;
      position: initial;
      margin: 0;
      transition: .5s;
      text-align: right;
      background: none;
      padding: 0;
      display: flex; }
      .Home .Navbar-list .closeMenu {
        display: none; }
      .Home .Navbar-list li a {
        padding: 15px 10px;
        font-size: 14px; }
  .Dashboard .closeMenu {
    display: none; } }



