.Features {
  padding: 40px 5%;
  text-align: center; }
  .Features-BoxFeatures {
    display: flex;
    justify-content: center;
    flex-wrap: wrap; }
  .Features .image {
    display: none;
    margin: auto;
    position: relative;
    top: 30px; }

@media (min-width: 768px) {
  .Features-BoxFeatures {
    flex-wrap: unset; }
  .Features .image {
    display: block; } }



