.CardInfo {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
  padding-top: 20px; }
  .CardInfo h3 {
    width: 75%;
    text-align: right; }
  .CardInfo p {
    width: 75%;
    text-align: right !important;
    max-width: 350px; }
  .CardInfo img {
    position: relative;
    top: -15px; }
  .CardInfo.inverse {
    flex-direction: row-reverse; }
    .CardInfo.inverse h3 {
      text-align: left; }
    .CardInfo.inverse p {
      text-align: left !important; }



