.Form h3 {
  margin-bottom: 20px;
  font-size: 20px;
  color: #fff; }

.Form .form-item {
  position: relative; }
  .Form .form-item input, .Form .form-item textarea {
    width: 100%;
    margin: 10px 0;
    padding: 10px;
    color: #fff;
    font-size: 16px;
    border: 0;
    border-bottom: 1px solid #fff;
    background: transparent;
    outline: none;
    transition: .3s;
    resize: none; }
  .Form .form-item textarea {
    height: 100px; }
  .Form .form-item .line {
    position: absolute;
    background: #fff;
    height: 2px;
    width: 100%;
    left: 0; }
  .Form .form-item .line::before, .Form .form-item .line::after {
    position: absolute;
    height: 2px;
    width: 0;
    bottom: 0;
    content: '';
    background: #009b3a;
    transition-property: all;
    transition-duration: 0.2s;
    transition-timing-function: cubic-bezier(0.55, 0, 0.1, 1); }
  .Form .form-item input::placeholder, .Form .form-item textarea::placeholder {
    color: #fff;
    opacity: .5; }

.Form .Button {
  padding: 10px 50px;
  margin: 10px auto;
  font-size: 14px; }



