.Hero {
  background: linear-gradient(to bottom right, #5f3fff, #8DDEFF); }
  .Hero .slogan-container {
    padding: 50px 5%;
    display: flex;
    align-items: center;
    justify-content: space-around; }
    .Hero .slogan-container img {
      display: none; }
  .Hero .slogan {
    width: 400px;
    display: inline-block; }
    .Hero .slogan .title {
      font-size: 90px;
      color: #fff; }
    .Hero .slogan .subTitle {
      font-weight: normal;
      color: #fff;
      font-size: 25px; }
    .Hero .slogan p {
      line-height: 1.5;
      color: #fff;
      margin-top: 20px; }
    .Hero .slogan .buttons {
      display: flex;
      margin-top: 40px;
      justify-content: center; }
      .Hero .slogan .buttons a.Button {
        text-decoration: none; }
    .Hero .slogan .Button.default:hover {
      box-shadow: 1px 2px 10px 4px rgba(50, 50, 50, 0.3); }

@media (min-width: 768px) {
  .Hero .slogan-container img {
    display: block; }
  .Hero .slogan .buttons {
    justify-content: initial; } }



