.About-container {
  background: #edf1f5;
  display: flex;
  justify-content: space-around; }
  .About-container .About {
    padding: 40px 5%;
    text-align: center;
    max-width: 800px; }
    .About-container .About .text {
      text-align: justify;
      margin: 20px 0;
      line-height: 1.5; }
    .About-container .About ul {
      list-style-position: inside; }
      .About-container .About ul li {
        margin-top: 10px; }
    .About-container .About .googlePlay {
      padding: 0 25px;
      margin: 30px 0;
      border: 2px solid #8ddeff; }
      .About-container .About .googlePlay p {
        margin: 8px 0;
        margin-left: 5px;
        font-size: 12px; }
      .About-container .About .googlePlay span {
        font-size: 15px; }
  .About-container .graphic {
    display: block;
    margin: 30px auto; }
  .About-container img {
    display: none; }

@media (min-width: 768px) {
  .About-container img {
    display: block; } }



